@import url(https://fonts.googleapis.com/css?family=Montserrat|Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Black+Han+Sans);
@import url(//cdn.jsdelivr.net/npm/semantic-ui@2.4.0/dist/semantic.min.css);
.pad-top-10 {
  padding-top: 10px; }

.pad-top-20 {
  padding-top: 20px; }

.pad-top-40 {
  padding-top: 40px; }

.push-top-10 {
  margin-top: 10px; }

.push-top-20 {
  margin-top: 20px; }

.push-top-40 {
  margin-top: 40px; }

.is-black {
  color: #000000; }

.is-white {
  color: #FFFFFF; }

p, h1, h2, h3, h4, h5, h6 {
  color: #e6e6e6; }

.App-header.ui.inverted.segment {
  background-color: #0f0a0a; }

.App-link {
  color: #2292A4;
  text-decoration: none;
  font-family: "Black Han Sans", sans-serif;
  font-size: 2em; }
  .App-link:hover {
    color: #D96C06; }
  .App-link:before {
    content: "TV";
    letter-spacing: 0.1em;
    color: #D96C06; }

.ui-cover-img {
  height: 300px;
  overflow: hidden; }
  .ui-cover-img img {
    width: 100%; }
  @media screen and (min-width: 768px) {
    .ui-cover-img {
      height: auto; }
      .ui-cover-img img {
        width: 100%; } }

.ui-episode-detail h1, .ui-episode-detail h2, .ui-episode-detail h3, .ui-episode-detail h4, .ui-episode-detail h5 {
  color: #D96C06; }

.ui-episode-detail__heading--small {
  font-size: 12px; }

.ui-episode-detail__heading--medium {
  font-size: 16px; }

.ui-episode-detail__heading--larg {
  font-size: 20px; }

.ui-episode-detail__heading--larger {
  font-size: 24px; }

.ui-episode-detail__heading--largest {
  font-size: 28px; }

.ui-episode-detail__heading--extra-large {
  font-size: 32px; }

.ui-episode-detail__paragraph--meduim {
  font-size: 16px; }

.ui-episode-detail__paragraph--large {
  font-size: 20px; }

.ui-episode-detail__paragraph--larger {
  font-size: 24px; }

.ui-episodes-list {
  height: 360px;
  overflow: scroll; }
  .ui-episodes-list::-webkit-scrollbar {
    width: 1em; }
  .ui-episodes-list::-webkit-scrollbar-track {
    -webkit-box-shadow: #2292A4; }
  .ui-episodes-list::-webkit-scrollbar-thumb {
    background-color: #D96C06;
    outline: 1px solid #D96C06;
    cursor: pointer;
    min-height: 100px; }
  .ui-episodes-list__item {
    border-bottom: 1px dashed #3d2929;
    padding: 1em 0; }
  .ui-episodes-list__header a {
    color: #D96C06;
    font-size: 20px; }
    .ui-episodes-list__header a:hover {
      color: #feeee0; }
  .ui-episodes-list__paragraph {
    font-size: 16px;
    padding-bottom: 0.5em; }

body {
  background-color: black;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

figure.ui-cover-img {
  margin: 0; }

body::-webkit-scrollbar {
  width: 1em; }

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3); }

body::-webkit-scrollbar-thumb {
  background-color: #D96C06;
  outline: 1px solid #D96C06;
  cursor: pointer; }

